@import url('https://fonts.googleapis.com/css2?family=Urbanist:wght@300;400;500;600;700;800;900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Urbanist', sans-serif;
}

body {
    overflow-x: hidden;
}

.container,
.container-full {
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 10px;
}

.container-full {
    max-width: 1400px;
}

/* ============ need ============== */


/* ============ content ============= */
.basket__shape {
    border-radius: 50%;
    position: absolute;
    padding: 0px 7px;
    top: -30%;
    right: -30%;
}

.navbar__fixed {
    top: 0;
    left: 0;
    width: 100%;
    position: fixed;
    z-index: 99;
}

/* main ---------------------------------------------- */
.main {
    padding: 20px 0;
    position: relative;
    z-index: 0;
}

@media (max-width:900px) {
    .main {
        height: auto;
    }
}

.main::after {
    content: '';
    width: 100%;
    height: 100%;
    background-image: url("assets/images/main-bg.svg");
    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
    mix-blend-mode: screen;
}

.main::before {
    content: '';
    width: 50%;
    height: 101%;
    background: #FFF1DB;
    clip-path: polygon(0 0, 80% 0, 100% 100%, 0% 100%);
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
}

.main .swiper-wrapper {
    padding: 20px 0;
    min-height: 350px;
}

@media (max-width:900px) {
    .main .swiper-wrapper {
        padding: 20px 0 90px 0;
    }
}

.main .swiper-slide {
    opacity: 0 !important;
}

.main .swiper-slide.swiper-slide-active {
    opacity: 1 !important;
}

.main .swiper-horizontal>.swiper-pagination-bullets,
.main .swiper-pagination-bullets.swiper-pagination-horizontal,
.main .swiper-pagination-custom,
.main .swiper-pagination-fraction {
    width: auto;
    left: 8.5%;
    bottom: 1.5%;
    font-size: 30px;
}

.main .swiper-pagination-current {
    color: #FD9D04;
}

.main .swiper-button-next,
.main .swiper-button-prev {
    width: 60px;
    height: 60px;
    background-color: #FD9D04;
    border-radius: 50%;
    border: 3px solid #FEC76F;
    bottom: 0;
    top: auto;
}

.main .swiper-button-next {
    left: 15%;
}

@media (max-width:640px) {

    .main .swiper-horizontal>.swiper-pagination-bullets,
    .main .swiper-pagination-bullets.swiper-pagination-horizontal,
    .main .swiper-pagination-custom,
    .main .swiper-pagination-fraction {
        display: none;
    }

    .main .swiper-button-next,
    .main .swiper-button-prev {
        display: none;
    }
}

.main .swiper-button-next:after,
.main .swiper-button-prev:after {
    font-size: 24px;
    color: #ffff;
}

.main__img {
    text-align: center;
}

.main__img img {
    width: 85%;
    /* height: 100%; */
    /* object-fit: cover; */
    animation: images 20s linear infinite;
}

@keyframes images {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}

.main .main__shape {
    position: absolute;
    bottom: 0;
    background-color: #fff;
    border-radius: 50%;
    width: 150px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-shadow: 3px 6px 15px rgba(196, 47, 51, 0.25);
    animation: shaked 4s linear infinite;
    z-index: 9;
}

@media (max-width:640px) {
    .main .main__shape {
        width: 100px;
        height: 100px;
    }
}

@keyframes shaked {
    0% {
        transform: skewX(-15deg);
    }

    5% {
        transform: skewX(15deg);
    }

    10% {
        transform: skewX(-15deg);
    }

    15% {
        transform: skewX(15deg);
    }

    20% {
        transform: skewX(0deg);
    }

    100% {
        transform: skewX(0deg);
    }
}

/* On bg -------------------------------------- */
.one__bg {
    position: relative;
    z-index: 0;
    overflow: hidden;
}

.one__bg::after {
    content: '';
    width: 100%;
    height: 100%;
    background-image: url('assets/images/one_bg.png');
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    animation: animateBg 2s linear infinite;
}

@keyframes animateBg {
    0% {
        transform: scale(1, 1);
    }

    50% {
        transform: scale(1.05, 1.05);
    }

    100% {
        transform: scale(1, 1);
    }
}

/* card ---------------------------------------- */
.card {
    padding: 30px;
    border: 3px dashed #E8E8E8;
    border-radius: 20px;
}

.card.popular {
    overflow: hidden;
}

.logo {
    width: 100px;
}

@media (max-width:480px) {
    .card {
        width: 70%;
    }

    .card__menu .card {
        width: 100%;
    }
}

.one__bg .swiper-slide-active .card {
    border: 3px solid #C42F33;
}

.card__img {
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    text-align: center;
}

.card__img img {
    width: 80%;
    object-fit: cover;
    margin: 0 auto;
}

.card__body h4 {
    word-wrap: break-word;
}

.swiper-button-next,
.swiper-button-prev {
    width: 60px;
    height: 60px;
    background-color: #C42F33;
    border-radius: 50%;
    border: 3px solid #DE7679;
}

.swiper-button-next:after,
.swiper-button-prev:after {
    font-size: 24px;
    color: #fff;
}

@media (max-width:480px) {

    .swiper-button-next,
    .swiper-button-prev {
        width: 40px;
        height: 40px;
    }

    .swiper-button-next:after,
    .swiper-button-prev:after {
        font-size: 18px;
    }
}



