/* .menu__item {
    min-width: 200px;
    overflow: hidden;
} */
.menu__item img {
    width: 100%;
    height: 100%;
    /* object-fit: cover; */
}

.menu__item .menu__img {
    width: 40px;
    height: 40px;
    /* background-color: #C42F33; */
