/* menu cards ------------------------------- */
.menuCards .swiper-button-next,
.menuCards .swiper-button-prev {
    display: none;
}

.card.product {
    border-style: solid;
    position: relative;
    z-index: 0;
    cursor: pointer;
}

.card.product::after {
    content: '';
    width: 70px;
    height: 35px;
    background: #F9F9F9;
    position: absolute;
    left: 50%;
    bottom: -34.9px;
    transform: translateX(-50%);
    border-bottom-left-radius: 500px;
    border-bottom-right-radius: 500px;
    border: 3px solid #E8E8E8;
    border-top: transparent;
}

.swiper-slide-active .card.product::after {
    border-color: #C42F33;
}

.card.product .card__key {
    position: absolute;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 50%;
    border: 2px solid #E8E8E8;
    z-index: 1;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* .card__img span{
    display: block;
    text-align: center;
} */

.card.product .card__shape {
    position: absolute;
    width: 50px;
    height: 50px;
    background: #C42F33;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 30px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    visibility: hidden;
    opacity: 0;
}

.card.product:hover .card__shape {
    opacity: 1;
    visibility: visible;
    transition: all 0.5s;
}

.card__quote {
    position: absolute;
    top: 30px;
    left: 30px;
}

/* branch ---------------------------------- */
.branch img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.map {
    height: 505px;
    border-radius: 20px;
    overflow: hidden;
    padding-left: 10px;
}

@media (max-width:768px) {
