.branch__video__circle {
    width: 100px;
    height: 100px;
    background: rgba(255, 195, 57, 0.5);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
