    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}

.reviews__content--shape {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
}

.reviews__content--shape2 {
    position: absolute;
    bottom: -30%;
    right: 0;
    z-index: 0;
}

.card.review {
