    min-height: 150px;
}

.card.review .review__img {
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 50%;
    background: #FEC76F;
}

/* abouts ------------------------------ */
.abouts__content {
    position: relative;
    z-index: 0;
}

.abouts__content::after {
    content: '';
    width: 80%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: #FD9D04;
    clip-path: polygon(0 0, 100% 0, 58% 100%, 0% 100%);
