}

.textarea {
    min-height: 100px;
    resize: none;
}

/* menu__bars ---------------------------- */
.menu__bars {
    width: 100%;
    height: 100vh;
    opacity: 0;
    visibility: hidden;
    position: fixed;
    z-index: -5;
}

.menu__bars.active {
    background-color: #FEC76F;
    top: 0;
    left: 0;
    z-index: 999;
    opacity: 1;
    visibility: visible;
    padding: 20px;
}

/* basket__bars ----------------------- */
.basket__bars {
    width: 100%;
    background: rgba(42, 107, 65, 0.4);
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    z-index: -5;
}

.basket__bars.active {
    opacity: 1;
    visibility: visible;
    z-index: 997;
}

.basket__content {
    width: 50%;
    height: 100vh;
    overflow-y: scroll;
    position: absolute;
    right: -100%;
    top: 0;
    background-color: white;
    /* padding: 20px; */
    transition: all 0.5s;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

@media (max-width:768px) {
    .basket__content {
        width: 100%;
    }
}

.basket__bars.active .basket__content {
    right: 0;
    transition: all 0.5s;
}

.basket__content::-webkit-scrollbar {
    display: none;
}


/* card additional -------------------- */
.card.additional {
    width: 170px;
}

.card.additional .card__img {
    height: 100px;
}

.card.active {
    border: 3px solid #C42F33;
}

.card.active::after {
    border-bottom: 3px solid #C42F33;
    border-right: 3px solid #C42F33;
    border-left: 3px solid #C42F33;
}

/* modal -------------------------*/
.modal {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -5;
    background: rgba(42, 107, 65, 0.4);
}

.modal.active {
    opacity: 1;
    visibility: visible;
    z-index: 998;
    transition: all 0.5s;
}

.modal__content {
    width: 50%;
    background: white;
    padding: 30px;
    border-radius: 10px;
    transform: scale(0);
    transition: all 0.5s;
}

@media (max-width:768px) {
    .modal__content {
        width: 80%;
    }
}

.modal.active .modal__content {
    transform: scale(1, 1);
    transition: all 0.5s;
}

