}

/* checkbox -----------------------*/
.checkbox__label {
    display: flex;
    align-items: flex-start;
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    font-size: 18px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.checkbox__label input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    position: absolute;
    top: 3px;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #fff;
    border: 3px solid #C42F33;
    border-radius: 50%;
}

.checkbox__label:hover input ~ .checkmark {
    background-color: white;
}

/* When the checkbox is checked, add a blue background */
.checkbox__label input:checked ~.checkmark {
    background-color: white;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.checkbox__label input:checked ~ .checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.checkbox__label .checkmark:after {
    left: 50%;
    top: 50%;
    transform: translate(-50% , -50%);
    width: 15px;
    height: 15px;
    margin-left: 0.3px ;
    background-color: #C42F33;
    border-radius: 50%;
}